import babelpolyfill from 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
//import './assets/theme/theme-darkblue/index.css'
import VueRouter from 'vue-router'
import store from './vuex/store'
import Vuex from 'vuex'
//import NProgress from 'nprogress'
//import 'nprogress/nprogress.css'
import routes from './routes'
//import Mock from './mock'
//Mock.bootstrap();
import 'font-awesome/css/font-awesome.min.css'
import axios from 'axios'
import md5 from 'js-md5';
import qs from 'qs';
import zyet from '@/common/js/zyet.js';

import MenuUtils from '@/utils/MenuUtils'
import SIdentify from './components/loginCheck'

Vue.use(Vuex)
Vue.use(SIdentify)
Vue.use(ElementUI)
Vue.use(VueRouter)
Vue.prototype.$http = axios;

let usertoken = '',cid = '',cookies='';
if(localStorage.getItem('token')){
	usertoken = localStorage.getItem('token');
	cid = localStorage.getItem('userid');
	cookies = localStorage.getItem('cookies');
}else{
	usertoken = 'a095bc6f516a87ba65f91c85e5621265';
	cid = '';
	cookies='';
}

axios.defaults.headers.common['token'] = usertoken;
axios.defaults.headers.common['cid'] = cid;
axios.defaults.headers.common['user_cookie'] = cookies;

//参数进行排序
function objKeySort(obj) {//排序的函数
	let objarr = qs.parse(obj);
    var newkey = Object.keys(objarr).sort();

    //先用Object内置类的keys方法获取要排序对象的属性名，再利用Array原型上的sort方法对获取的属性名进行排序，newkey是一个数组
    var newObj = {};//创建一个新的对象，用于存放排好序的键值对
    for (var i = 0; i < newkey.length; i++) {//遍历newkey数组
        newObj[newkey[i]] = objarr[newkey[i]];//向新创建的对象中按照排好的顺序依次增加键值对
    }
    let rs='';
    for (var key in newObj) {
        if(newObj[key]!=''){
            rs += key+newObj[key];
        }
    }
    return rs;//返回排好序的新对象
}

//获取url最后的字符
function splitUrl(url){
    let b_arr = new Array(),b_str='';
    b_arr = url.split('/');
    b_str = b_arr[b_arr.length-1];
	return b_str;
}
let isEncrypt = true;
axios.interceptors.request.use(function (config) {
    if(isEncrypt) {
        let b_str = splitUrl(config.url), mid_str = '', e_str = 'zilot', times = new Date().getTime();
        if (config.method == 'get' || config.method == 'delete') {

            if (config.params) {
                config.params.timestamp = times;
                mid_str = objKeySort(qs.stringify(config.params));
                let signstr = md5(b_str + mid_str + e_str).toUpperCase();
                config.params.sign = signstr;
            } else {

                let params = {
                    timestamp: times
                }
                config.params = params;
                mid_str = 'timestamp' + times;
                let signstr = md5(b_str + mid_str + e_str).toUpperCase();
                config.params.sign = signstr;
            }

            let newobj = qs.parse(config.params);
            let para = {};
            for (var key in newobj) {
                if (newobj[key] != '') {
                    para[key] = zyet.encrypt(newobj[key] + '')
                }
            }
            config.params = para;
        } else {

            if (typeof(config.data) == 'object') {

            } else {
                config.data = config.data + '&timestamp=' + times;
                mid_str = objKeySort(config.data);
                let signstr = md5(b_str + mid_str + e_str).toUpperCase();
                config.data = config.data + '&sign=' + signstr;
                let newobj = qs.parse(config.data);
                let para = {}
                for (var key in newobj) {
                    if (newobj[key] != '') {
                        para[key] = zyet.encrypt(newobj[key] + '')
                    }

                }
                config.data = qs.stringify(para);
            }

        }
    }
    return config;
  }, function (error) {
    // 当请求异常时做一些处理
    return Promise.reject(error);
  });

// 响应时拦截
axios.interceptors.response.use(function (response) {
    // 返回响应时做一些处理
    return response;
  }, function (error) {
    // 当响应异常时做一些处理
    return Promise.reject(error);
  });

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
	// let data = JSON.parse(localStorage.getItem('routers'));
	//NProgress.start();
	if (to.path == '/login') {
	    localStorage.removeItem('token');
	}
	if (to.path) {
	  next()
	} else {
	  next({ path: '/404' })
	}
	
})


new Vue({
  //el: '#app',
  //template: '<App/>',
  router,
  store,
  //components: { App }
  render: h => h(App)
}).$mount('#app')




