<template>
	<section>		
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline='true' >			
				<el-form-item style="float: right;">
					<el-button type="primary" @click="add_manager">添加新用户</el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<el-table :data="info" border stripe highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column prop="tablenum" label="#" width="100">
			</el-table-column>
			<el-table-column prop="userName" label="用户名">
			</el-table-column>
			<el-table-column prop="nickName" label="真实姓名">
			</el-table-column>
			<el-table-column prop="phone" label="手机号">
			</el-table-column>
			<el-table-column prop="createDate" label="创建日期" >
			</el-table-column>
			<el-table-column prop="lastLogin" label="最后登录时间" >
			</el-table-column>
			<el-table-column prop="statusname" label="状态" >
			</el-table-column>
				<el-table-column prop="remark" label="备注" >
			</el-table-column>
			<el-table-column label="操作" width="380">
				<template slot-scope="scope">
					<el-button size="small" @click="handleOn(scope.row)">{{scope.row.statusOn}}</el-button>
					<el-button size="small" @click="handleReset(scope.$index, scope.row)">重置密码</el-button>
					<el-button type="primary" size="small" @click="handleEdit(scope.$index, scope.row)">修改信息</el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<el-col :span="24" class="toolbar">
			<el-pagination layout="total,prev, pager, next" @current-change="handleCurrentChange" :page-size="pagesize" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
		<el-dialog title="重置后台用户密码" :visible.sync="resetFormVisible" :close-on-click-modal="false">
			<el-form :model="resetForm" label-width="100px"  ref="resetForm" :rules="rules2">								
				<el-form-item label="输入新密码" class="pwd" prop="password">
					<el-input  v-model="resetForm.password" auto-complete="off" placeholder="必填:长度在6到20"></el-input>
				</el-form-item>						
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="resetFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="resetSubmit" :loading="addLoading">使用新密码</el-button>
			</div>
		</el-dialog>
		<!--新增用户-->
		<el-dialog title="新增" :visible.sync="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px"  ref="addForm" :rules="rules">				
				<el-form-item label="用户名" prop="userName">
					<el-input  v-model="addForm.userName" placeholder="必填:长度2~20"  auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="密码" prop="password">
					<el-input  v-model="addForm.password" placeholder="必填:长度6~20"></el-input>
				</el-form-item>
				<el-form-item label="真实姓名" prop="nickName">
					<el-input  v-model="addForm.nickName" placeholder="必填:长度2~20"></el-input>
				</el-form-item>
				<el-form-item label="手机号" prop="phone">
					<el-input  v-model="addForm.phone" placeholder="必填"></el-input>
				</el-form-item>
				<el-form-item label="备注">
					<el-input type="textarea" v-model="addForm.remark" ></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
			</div>
		</el-dialog>
		<!--修改信息-->
		<el-dialog title="编辑" :visible.sync="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px"  ref="editForm" :rules="rules">				
				<el-form-item label="用户名" prop="userName" v-if="editForm.userName!='superadmin'">
					<el-input  v-model="editForm.userName" placeholder="必填:2~20"  auto-complete="off" ></el-input>
				</el-form-item>
				<el-form-item label="真实姓名" prop="nickName" >
					<el-input  v-model="editForm.nickName" placeholder="必填:2~20"></el-input>
				</el-form-item>
				<el-form-item label="手机号" prop="phone" >
					<el-input  v-model="editForm.phone" placeholder="必填"></el-input>
				</el-form-item>
				<el-form-item label="备注" prop="remark" >
					<el-input type="textarea" v-model="editForm.remark" ></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="addLoading">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import qs from 'qs';
	import md5 from 'js-md5';
	export default {
		data(){
			return {			
				info:[],
				listLoading:false,
				sels:[],
				total:0,
				page:1,
				pagesize:10,
				resetFormVisible:false,
				addFormVisible:false,
				editFormVisible:false,
				editLoading:false,
				addLoading:false,
				resetLoading:false,
				roleList:{},
				resetForm:{
					password:''
				},
				rules: {
		          userName: [
		            { required: true, message: '请输入用户名', trigger: 'blur' },
		            { min: 2, max: 20, message: '长度在 2 到 20 ', trigger: 'blur' }
		          ],
		          password: [
		            { required: true, message: '请输入密码', trigger: 'blur' },
		            { min: 6, max: 20, message: '长度在 6 到 20 ', trigger: 'blur' }
		          ],
		          nickName: [
		            { required: true, message: '请输入真实姓名', trigger: 'blur' },
		            { min: 2, max: 20, message: '长度在 2 到 20 ', trigger: 'blur' }
		          ],
		          phone: [
		            	{ required: true, message: '请输入手机号', trigger: 'blur' },
		            { min: 11, max: 11, message: '手机号格式不正确', trigger: 'blur'},
		            {type: 'number',message: '手机号应为数字',trigger: 'blur',transform(value) { return Number(value);}}
		          ],
		          status: [
		            { required: true, message: '请选择状态', trigger: 'change' },
		          ]
		        },
		        rules2:{
		        		 password: [
			            { required: true, message: '请输入密码', trigger: 'blur' },
			            { min: 6, max: 20, message: '长度在 6 到 20 ', trigger: 'blur' }
			          ]
		        },
				addForm:{
					userName:'',
					password:'',
					nickName:'',
					phone:'',
					remark:''
				},
				editForm:{
					userName:'',
					nickName:'',
					phone:'',
					remark:''
				},
			}
		},
		watch:{
			$route(to,from){
			   if(to.path=='/backManagement'){
			   		let self = this;
					var func = async function(){
						try{
							self.getTableData();	
						}catch(e){
							self.$message.error('请重新刷新页面');
						}
					}
					func();
			   	}
		  	}	
		},
		mounted(){
			this.getTableData();
		},
		methods:{			
			getTableData(){
	        		let sendData= {
	        			number: this.pagesize,
	        			page:this.page,        			
	        		};
	        		let self = this;
	        		this.$http.get('/v1/back/user/list',{
	        			headers: {'Accept': 'application/json'},
	        			params:sendData
	        		})
	        		.then(function(res){
	        			self.listLoading = false;
	        			if(res.data.code==200){
	        				if(res.data.data!=null&&res.data.data!=''){
	        					self.info = res.data.data;
		        				self.total = res.data.count;
			        			for(var i=0;i<self.info.length;i++){
			        				let j =i+1;
			        				self.info[i].tablenum = j; 
			        				if(self.info[i].status==1){
			        					self.info[i].statusname = '已启用';
										self.info[i].statusOn = '禁用';
			        				}else{
										self.info[i].statusname = '已禁用';
										self.info[i].statusOn = '启用';
									}
			        				if(self.info[i].createTime!=null&&self.info[i].createTime!=''){
			        					self.info[i].createDate = self.format(self.info[i].createTime,'yyyy-MM-dd')
			        				}
			        				if(self.info[i].lastLoginTime!=null&&self.info[i].lastLoginTime!=''){
			        					self.info[i].lastLogin =  self.format(self.info[i].lastLoginTime,'yyyy-MM-dd hh:mm:ss')
			        				}else{
			        					self.info[i].lastLogin = '';
			        				}
			        			}
	        				}else{
	        					self.info=[];
	        				}
	        			}else if(res.data.code==401){
	        				self.$message.error(res.data.message);
	        				setTimeout(function(){
	        					self.$router.push({ path: '/login' });
	        				},2000)
	        			}else{
	        				self.$message.error(res.data.message);
	        			}
	        		})
	        		.catch(function(res){
	        			self.listLoading = false;
	        			self.$message.error(res.data.message);
	        		})	
			},
			add_manager(){		
				this.addFormVisible = true;
			},
			addSubmit(){
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let self = this;
							let para = Object.assign({}, this.addForm);
							let sendData = {
								userName:para.userName,
								password:md5(para.password),
								nickName:para.nickName,
								phone:para.phone,
								remark:para.remark
							};
							this.$http({
								method:'post',
								url:'/v1/back/user/add',
								data:qs.stringify(sendData),
								headers:{'Accept': 'application/json'},
							})
							.then((res) => {
									console.log(res)
									self.addLoading = false;
									self.addFormVisible = false;
									self.addForm = {
										userName:'',
										password:'',
										nickName:'',
										phone:'',
										remark:''
									}
								if(res.data.code==200){
									self.$message({
										message:res.data.message ,
										type: 'success'
									});
									
									self.getTableData();
								}else if(res.data.code==401){
				        				self.$message.error(res.data.message);
				        				setTimeout(function(){
				        					self.$router.push({ path: '/login' });
				        				},2000)
				        			}else{
				        				self.$message.error(res.data.message);
				        			}
							});
						});
					}
				});	
			},
			editSubmit(){
				let self = this;
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.editLoading = true;
							let para = Object.assign({}, this.editForm);	
							console.log(para)
							let sendData = {
								id:para.id,
								userName:para.userName,
								nickName:para.nickName,
								phone:para.phone,
								remark:para.remark
							};
							this.$http({
								method:'put',
								url:'/v1/back/user/modify',
								data:qs.stringify(sendData),
								headers:{
									'Accept': 'application/json',
									'Content-Type':'application/x-www-form-urlencoded'
									},
								})
							.then((res) => {
								//console.log(res,para)
								this.editFormVisible = false;
								this.editLoading = false;
								if(res.data.code==200){
									//NProgress.done();
									this.$message({
										message: res.data.message,
										type: 'success'
									});
									this.$refs['editForm'].resetFields();
									this.getTableData();
								}else{
									this.$message({
										message: res.data.message,
										type: 'error'
									});
								}
							});
						});
					}
				});
			},
			resetSubmit(){
				let self = this;
				this.$refs.resetForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认重置吗？', '提示', {}).then(() => {
							this.editLoading = true;
							let para = Object.assign({}, this.resetForm);	
							console.log(para)
							let sendData = {
								id:para.id,
								password:md5(para.password)
							};
							this.$http({
								method:'put',
								url:'/v1/back/user/modify-password',
								data:qs.stringify(sendData),
								headers:{
									'Accept': 'application/json',
									'Content-Type':'application/x-www-form-urlencoded'
									},
								})
							.then((res) => {
								//console.log(res,para)
								this.resetFormVisible = false;
								this.resetLoading = false;
								if(res.data.code==200){
									//NProgress.done();
									this.$message({
										message: res.data.message,
										type: 'success'
									});
									this.$refs['resetForm'].resetFields();
									//this.editFormVisible = false;
									this.getTableData();
								}else{
									this.$message({
										message: res.data.message,
										type: 'error'
									});
								}
							})
						})
					}
				})
			},
			handleOn(row){
				let status,nowstatus;
				if (row.status==1){
					status = 0;
					nowstatus = '禁用';
				}else{
					status = 1;
					nowstatus = '启用';
				}
				this.$confirm('确认'+nowstatus+'该用户吗?',nowstatus,{
					type:'warning',
				}).then(()=>{
					let para = {userId :row.id,type:status};
					console.log(status)
					let self = this;
					this.$http({
						method:'put',
						url:'/v1/back/user/disable',
						data:qs.stringify(para),
						headers:{
							'Accept': 'application/json',
							'Content-Type':'application/x-www-form-urlencoded'
						},
					}).then((res)=>{
						if(res.data.code==200){
							if(row.status=='0'){
								row.statusOn ='启用';
							}else if(row.status=='1'){
								row.statusOn ='禁用';
							};
							self.getTableData();
							self.$message({
								message: res.data.message,
								type: 'success'
							});
						}else{
							self.$message({
								message: res.data.message,
								type: 'error'
							});
						}
					})
				})
			},
			handleReset(index,row){
				this.resetFormVisible = true;
				this.resetForm.id = row.id;
				this.resetForm.password ='';
				console.log(this.resetForm)
			},
			handleEdit(index,row){	
				// console.log(row)
				this.editFormVisible = true;			
				this.editForm = Object.assign({},row);
			},
			handleDel(index,row){
				this.$confirm('确认删除该用户吗?', '删除用户', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					console.log(row.id)
					let para = { id: row.id };
					let self = this;
					this.$http({
						method:'delete',
						url:'/v1/back/user/delete',
						params:para,
						headers:{'Accept': 'application/json'},
					})
					.then((res) => {
						self.listLoading = false;
						if(res.data.code==200){
							
							//NProgress.done();
							self.$message({
								message: '删除成功',
								type: 'success'
							});
							self.getTableData();
						}else{
							self.$message({
								message: res.data.message,
								type: 'error'
							});
						}
					});
				}).catch((error) => {
					self.listLoading = false;
					self.$message({
						message: res.data.message,
						type: 'error'
					});
				});
			},
			selsChange(sels){
				this.sels = sels;
			},
			handleCurrentChange(val){
				this.page = val;
				this.getTableData()
			},
			format(time,format){
				var t = new Date(time);
				var tf = function(i){return (i < 10 ? '0' : '') + i};
				return format.replace(/yyyy|MM|dd|hh|mm|ss/g, function(a){
					switch(a){
						case 'yyyy':
							return tf(t.getFullYear());
							break;
						case 'MM':
							return tf(t.getMonth() + 1);
							break;
						case 'mm':
							return tf(t.getMinutes());
							break;
						case 'dd':
							return tf(t.getDate());
							break;	
						case 'hh':
							return tf(t.getHours());
							break;
						case 'mm':
							return tf(t.getMinutes());
							break;
						case 'ss':
							return tf(t.getSeconds());
							break;
						}
				})
			},
		}
	}
</script>	
<style scoped>
	section{
		font-size: 18px;
		font-weight: 600;
		.pwd{
			.el-form-item__label{
				width: 100px !important;
			}
			.el-form-item__content{
				margin-left: 100px !important;
			}
		}
	}
	div .el-select{
		width: 100% !important;
	}
</style>


