import Login from './views/Login'
import NotFound from './views/404'
import Home from './views/Home'

import usermanagement from './views/usermanagement/backManagement'

let routes = [
	{
    		path:'/',
    		redirect:'/login',
    		hidden:true
    },
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/home',
        component: Home,
        name: '基础设置',
        iconCls: 'fa fa-home',//图标样式class
        hidden: true,
        children: [

        ]
    },
	// {
	// 	path: '/backManagement',
	// 	component: Home,
	// 	name: '用户管理',
	// 	iconCls: 'fa fa-user-o',
	// 	children: [
	// 		{ 	path: '/backManagement',
	// 			component: resolve => require(['@/views/usermanagement/backManagement'], resolve),
	// 			name: '用户信息',
	// 			meta: { keepAlive: true }
	// 		},
	// 	 ]
	// },
	{
		path: '/activitymanagement',
		component: Home,
		name: '活动管理',
		iconCls: 'fa fa-bar-chart',
		children: [
			{ 	path: '/activityConfig',
				component: resolve => require(['@/views/activitymanagement/activityConfig'], resolve),
				name: '活动配置',
				meta: { keepAlive: true }
			},
			// { 	path: '/activityStatistic',
			// 	component: resolve => require(['@/views/activitymanagement/activityStatistic'], resolve),
			// 	name: '活动信息',
			// 	meta: { keepAlive: true }
			// },
			{ 	path: '/activityProduct',
				component: resolve => require(['@/views/activitymanagement/activityProduct'], resolve),
				name: '活动商品',
				meta: { keepAlive: true }
			},
		 ]
	},
	{
		path: '/ordermanagement',
		component: Home,
		name: '订单管理',
		iconCls: 'fa fa-list-alt',
		children: [
			{ 	path: '/orderList',
				component: resolve => require(['@/views/ordermanagement/orderList'], resolve),
				name: '订单列表',
				meta: { keepAlive: true }
			}			
		 ]
	},
//  {
//      path: '*',
//      hidden: true,
//      redirect: { path: '/404' }
//  }
];

export default routes;



