<template>
	<el-row class="container">
		<el-col :span="24" class="header">
			<el-col :span="10" class="logo" :class="collapsed?'logo-collapse-width':'logo-width'">
				{{collapsed?'':sysName}}
			</el-col>
			<el-col :span="10">
			</el-col>
			<el-col :span="4" class="userinfo">
				<div style="float: right;margin-left: 10px;">{{sysUserName}}</div>
				<el-dropdown trigger="hover">
					<span class="el-dropdown-link userinfo-inner"><img src="../../static/images/user.jpeg" /> </span>
					<el-dropdown-menu slot="dropdown">
						<!--<el-dropdown-item>我的消息</el-dropdown-item>
						<el-dropdown-item>设置</el-dropdown-item>-->
						<!-- <el-dropdown-item  @click.native="toOther">数据分析</el-dropdown-item> -->
						<el-dropdown-item  @click.native="logout">退出登录</el-dropdown-item>
					</el-dropdown-menu>
				</el-dropdown>
			</el-col>
		</el-col>
		<el-col :span="24" class="main">
			<aside :class="collapsed?'menu-collapsed':'menu-expanded'">
				<!--导航菜单-->
				<el-menu :default-active="$route.path" class="el-menu-vertical-demo" @open="handleopen" @close="handleclose" @select="handleselect"
					 unique-opened router v-show="!collapsed">
					<template v-for="(item,index) in nodes" v-if="!item.hidden">
						<el-submenu :index="index+''" v-if="!item.leaf">
							<template slot="title"><i :class="item.iconCls"></i>{{item.name}}</template>
							<el-menu-item v-for="child in item.children" :index="child.path" :key="child.path" v-if="!child.hidden">{{child.name}}</el-menu-item>
						</el-submenu>
						<el-menu-item v-if="item.leaf&&item.children.length>0" :index="item.children[0].path" ><i :class="item.iconCls"></i>{{item.children[0].name}}</el-menu-item>
					</template>
				</el-menu>
				<!--导航菜单-折叠后-->
				<ul class="el-menu el-menu-vertical-demo collapsed" v-show="collapsed" ref="menuCollapsed">
					<li v-for="(item,index) in $router.options.routes" v-if="!item.hidden" class="el-submenu item">
						<template v-if="!item.leaf">
							<div class="el-submenu__title" style="padding-left: 20px;" @mouseover="showMenu(index,true)" @mouseout="showMenu(index,false)"><i :class="item.iconCls"></i></div>
							<ul class="el-menu submenu" :class="'submenu-hook-'+index" @mouseover="showMenu(index,true)" @mouseout="showMenu(index,false)">
								<li v-for="child in item.children" v-if="!child.hidden" :key="child.path" class="el-menu-item" style="padding-left: 40px;" :class="$route.path==child.path?'is-active':''" @click="$router.push(child.path)">{{child.name}}</li>
							</ul>
						</template>
						<template v-else>
							<li class="el-submenu">
								<div class="el-submenu__title el-menu-item" style="padding-left: 20px;height: 56px;line-height: 56px;padding: 0 20px;" :class="$route.path==item.children[0].path?'is-active':''" @click="$router.push(item.children[0].path)"><i :class="item.iconCls"></i></div>
							</li>
						</template>
					</li>
				</ul>
			</aside>
			<section class="content-container">
				<div class="grid-content bg-purple-light">
					<el-col :span="24" class="breadcrumb-container" :style="{ height:bodyHeight+'px' }">
						<ul>
							<li v-for="item in routerArr" :key="item.name">
								<el-button class="open_btn" @click="routeTo(item)" :class="$route.name==item?'btn-active':'no-bg'">{{item}}</el-button>
								<button class="close_btn" @click="removeRoute(item)"><i class="fa fa-times" aria-hidden="true"></i></button>
							</li>
						</ul>
					</el-col>
					<el-col :span="24" class="content-wrapper" :style="{ marginTop:bodyHeight+'px' }">
						<transition name="fade" mode="out-in" >
							<keep-alive >
								<router-view></router-view>
							</keep-alive>
						</transition>
						<transition name="fade" mode="out-in" v-if="$route.path=='/home'">
							<img  style="width: 100%;" src="../../static/images/wecome.jpeg"/>
						</transition>
					</el-col>
				</div>
			</section>
		</el-col>
	</el-row>
</template>

<script>
	import qs from 'qs';
	import axios from 'axios'
	export default {
		data() {
			return {
				sysName:'自助邦运维系统',
				collapsed:false,
				sysUserAvatar: '',
				routerArr:[],
				pathName:[],
				bodyHeight:40,
				Url:'https://sas.qyfzilot.cn/b/#/',
                rootPath:'https://um.qyfzilot.cn',
				localtype:'2',
				form: {
					name: '',
					region: '',
					date1: '',
					date2: '',
					delivery: false,
					type: [],
					resource: '',
					desc: '',
				},
				nodes: this.$router.options.routes
			}
		},
        computed:{
            sysUserName:function(){
                return localStorage.getItem('user');
            }
        },
		mounted(){
           this.getUrlpath();
		},
		// created(){
		// 	let isLoadNodes = localStorage.getItem('isLoadNodes');
		// 	this.getArr();
		// 	if (!isLoadNodes) {
		// 		let data = JSON.parse(localStorage.getItem('routers'))
		// 		this.nodes.push(...data)
		// 		localStorage.setItem('isLoadNodes', 'true')
		// 	}
		// 	if(this.$route.name!='基础设置'){
		// 		this.routerArr.push(this.$route.name)
		// 	}
		// 	if(this.$route.path=='/home'){
		// 		this.bodyHeight = '0'
		// 	}else{
		// 		this.bodyHeight = '40'
		// 	}
		// },
		watch:{
			$route(to,from){
			  	if(this.routerArr.length!=0){
			  		let data = this.routerArr;
			  		var testStr=','+data.join(",")+",";
					if(testStr.indexOf(","+to.name+",")==-1){
						if(to.name!='基础设置'){
							data.push(to.name);
						}
					}
			  	}
			  	if(this.$route.path=='/home'){
					this.bodyHeight = '0'
				}else{
					this.bodyHeight = '40'
				}
			},
		},
		methods: {
			getUrlpath(){
				let sourceStr = location.href.split('/')[2];
				if(sourceStr =='wxpromotion.qyfzilot.cn'){
					
					this.rootPath='https://um.qyfzilot.cn';
					this.Url = 'https://sas.qyfzilot.cn/b/#/';
					
				}else if(sourceStr =='test-wxpromotion.qyfzilot.cn'){
					
					this.rootPath='https://test-um.qyfzilot.cn';
					this.Url = 'https://test-sas.qyfzilot.cn/b/#/';
					
				}else{
					if(this.localtype==1){
						this.rootPath='https://um.qyfzilot.cn';
						this.Url = 'https://sas.qyfzilot.cn/b/#/';
					}else{
						this.rootPath='https://test-um.qyfzilot.cn';
						this.Url = 'https://test-sas.qyfzilot.cn/b/#/';
					}
				}
			},
			onSubmit() {
//         		console.log('submit!');
			},
			handleopen() {
//				console.log(a,b);
			},
			handleclose() {
//				console.log(a,b);
			},
			handleselect: function (a, b) {
				let data = this.pathName;
				let result = this.routerArr;
				for(var i=0;i<data.length;i++){
					if(a==data[i].path&&a!='/home'){
						if(result.length!=0){
							var testStr=','+result.join(",")+",";
							if(testStr.indexOf(","+data[i].name+",")==-1){
								result.push(data[i].name);
							}
						}else{
							result.push(data[i].name);
						}
					}
				}
			},
			getArr(){
				let arr = JSON.parse(localStorage.getItem('routers'));
				for(var i=0;i<arr.length;i++){
					let l = arr[i].children;
					for(var j=0;j<l.length;j++){
						this.pathName.push({path:l[j].path,name:l[j].name})
					}
				}
			},
			routeTo(val){
				let routesArr = this.pathName;
				for(var i=0;i<routesArr.length;i++){
					if(val==routesArr[i].name){
						this.$router.push(routesArr[i].path);
					}
				}
			},
			removeRoute(val){
				if(val==this.$route.name){
					return false;
				}
				let result = this.routerArr;
				let index = result.indexOf(val);
				if (index > -1) {
				    result.splice(index, 1);
				}
			},
			cutArr(val){
				for(var i=0;i<val.length;i++){
                    for(var x=i+1;x<val.length;x++){
                        if(val[i]==val[x]){
                            val.splice(x,1)
                        }
                    }
                }
			},
            toOther: function () {
                var self = this;
                this.$confirm('是否切换到数据分析中心?', '提示', {
                    //type: 'warning'
                }).then(() => {
                    let userid = localStorage.getItem('userid');
                    let para = {
                        userId: userid,
                        type:'1'
                    };
                    this.$http({
                        method:'post',
                        url:self.rootPath+'/v1/back/user/login-verification',
                        data:qs.stringify(para),
                        headers:{'Accept': 'application/json'},
                    })
					.then((res) => {
						if(res.data.code==200){
							let data = res.data.data;
							console.log(data)
							window.open(self.Url+"?token="+data.token+"&id="+data.id+"&cookies="+data.cookie);
						}else if(res.data.code==401){
                            self.$message.error(res.data.message);
                            setTimeout(function(){
                                self.$router.push({ path: '/login' });
                            },2000)
                        }else{
                            self.$message({
                                message: res.data.message,
                                type: 'error'
                            });
                            self.$router.push('/login');
                        }
                    });
                }).catch((res) => {
                    self.$message({
                        message: res.data.message,
                        type: 'error'
                    });
                    self.$router.push('/login');
                });
            },
			//退出登录
			logout: function () {
				var self = this;
				this.$confirm('确认退出吗?', '提示', {
					//type: 'warning'
				}).then(() => {
					let userid = localStorage.getItem('userid');
					let para = {
						userId: userid,
                        type:'2'
					};
					this.$http({
						method:'post',
						url:self.rootPath+'/v1/back/user/logout',
						data:qs.stringify(para),
						headers:{'Accept': 'application/json'},
					})
					.then((res) => {
							if(res.data.code==200){
								self.$router.options.routes=[];
								localStorage.removeItem('token');
								localStorage.removeItem('user');
								localStorage.removeItem('userid');
                                localStorage.removeItem('user_cookie');
								localStorage.removeItem('routers');
								localStorage.removeItem('isLoadNodes');
								self.$router.push('/login');
							}else{
                                self.$router.push('/login');
                            }
                    });
                }).catch((res) => {
                    self.$router.push('/login');
                });
			},
			//折叠导航栏
			collapse:function(){
				this.collapsed=!this.collapsed;
				var menu = document.getElementsByClassName('el-menu el-menu-vertical-demo')
				if(!this.collapsed){
					menu[0].style.width='230px'
				}
			},
			showMenu(i,status){
				this.$refs.menuCollapsed.getElementsByClassName('submenu-hook-'+i)[0].style.display=status?'block':'none';
			}
		},

	}

</script>

<style scoped lang="scss">
	@import '~scss_vars';

	.container {
		position: absolute;
		top: 0px;
		bottom: 0px;
		width: 100%;
		.header {
			height: 60px;
			line-height: 60px;
			background: $color-primary;
			color:#fff;
			.userinfo {
				text-align: right;
				padding-right: 35px;
				float: right;
				.userinfo-inner {
					cursor: pointer;
					color:#fff;
					img {
						width: 40px;
						height: 40px;
						border-radius: 20px;
						margin: 10px 0px 10px 10px;
						float: right;
					}
				}
			}
			.logo {
				//width:230px;
				height:60px;
				font-size: 20px;
				padding-left:20px;
				padding-right:20px;
				border-color: rgba(238,241,146,0.3);
				border-right-width: 1px;
				border-right-style: solid;
				img {
					width: 40px;
					float: left;
					margin: 10px 10px 10px 18px;
				}
				.txt {
					color:#fff;
				}
			}
			.logo-width{
				width:230px;
			}
			.logo-collapse-width{
				width:60px
			}
			.tools{
				padding: 0px 23px;
				width:14px;
				height: 60px;
				line-height: 60px;
				cursor: pointer;
			}
		}
		.main {
			display: flex;
			// background: #324057;
			position: absolute;
			top: 60px;
			bottom: 0px;
			overflow: hidden;
			aside {
				flex:0 0 230px;
				width: 230px;
				// position: absolute;
				// top: 0px;
				// bottom: 0px;
				.el-menu{
					height: 100%;
				}
				.collapsed{
					width:60px;
					.item{
						position: relative;
					}
					.submenu{
						position:absolute;
						top:0px;
						left:60px;
						z-index:99999;
						height:auto;
						display:none;
					}

				}
			}
			.menu-collapsed{
				flex:0 0 60px;
				width: 60px;
			}
			.menu-expanded{
				flex:0 0 230px;
				width: 230px;
				overflow: scroll;
			}
			.content-container {
				// background: #f1f2f7;
				flex:1;
				// position: absolute;
				// right: 0px;
				// top: 0px;
				// bottom: 0px;
				// left: 230px;
				overflow-y: scroll;
				padding: 20px;
				.breadcrumb-container {
					position: fixed;
					width: 87%;
					overflow: wrap;
					top: 60px;
					/*border-bottom: 1px solid #E1E1E1;*/
					background: #FFF;
					z-index: 100;
					padding:0 !important;
					margin-bottom: 5px;
					ul{
						padding: 0;
						margin:0;
					}
					ul li{
						margin-top: 20px;
						list-style: none;
						float: left;
						margin:5px 0;
						margin-left: 5px;
					}
					li:nth-child(1){
						margin-left: 0;
					}
					.btn-active{
						background:#ecf5ff;
						border: 1px solid #c6e2ff;
						cursor: pointer;
						.fa-times{
							color: #f56c6c;
						}
					}
					.no-bg{

					}
					.open_btn{
						padding-top: 12px;
						padding-right: 20px;
						padding-left: 6px;
						padding-bottom: 6px;
						float: left;
					}
					.close_btn{
						float: left;
						height: auto;
						padding: 0;
						width: 18px;
						border: 1px solid #f3f3f3;;
						background: #fff;
						border-radius: 3px;
						outline: none;
						position: relative;
						margin-top: 0;
						margin-left:-18px;
						.fa-times{
							color: #E1E1E8;
						}
					}
					.close_btn:hover{
						border: 1px solid #c6e2ff;
						cursor: pointer;
						.fa-times{
							color: #f56c6c;
						}
					}
					.title {
						width: 200px;
						float: left;
						color: #475669;
					}
					.breadcrumb-inner {
						float: right;
					}
				}
				.content-wrapper {
					box-sizing: border-box;
				}
			}
		}
	}
</style>



