<template>
	<div class="login">
		<div class="box_img">
			<img src="../../static/images/input_box.png" alt="" style="width: 100%;"/>
		</div>
		<el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left" label-width="0px" class="demo-ruleForm login-container">
		    <h3 class="title">
		    		<span class="logo_box"><img src="../../static/images/logo.png" alt="" class="logo"/></span>
		    		<span style="display: inline-block;position: relative;top: -10px;margin-left: 10px;color: #2370f7;font-size: 16px;font-weight:normal;">自助邦运维系统</span>
		    </h3>
		    <el-form-item prop="account">
		    			<div><img src="../../static/images/account.png" alt="" style="width: 30px;position: absolute;margin-top: 5px;z-index: 2;"/></div>
		      		<el-input type="text" v-model="ruleForm2.account" auto-complete="off" placeholder="账号"  ref='account'></el-input>
		    </el-form-item>
		    <el-form-item prop="checkPass">
		    		<div><img src="../../static/images/pwd.png" alt="" style="width: 30px;position: absolute;margin-top: 5px;z-index: 2;"/></div>
		      	<el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off" placeholder="密码" ></el-input>
		    </el-form-item>
		    <!-- <el-input type="text" placeholder="请输入4位验证码" class="yanzhengma_input" @keyup.enter.native="handleSubmit2" @blur="checkLpicma"  v-model="picLyanzhengma" style="width: 180px;"></el-input>
		    <div class="code" @click="refreshCode">
		    		<s-identify :identifyCode="identifyCode"></s-identify>
		    </div> -->
		     <!--

		     <el-button  id="code" @click.native="createCode"  class="verification1"  style="width: 120px;margin-left:40px;">{{checkCode}}</el-button>-->

		   <!-- <el-checkbox v-model="checked"  class="remember">记住密码</el-checkbox>-->
		    <el-form-item style="width:100%">
		      <el-button type="primary" style="width:100%;" class="submit_Btn" @click.native.prevent="handleSubmit2"  :loading="logining">登录</el-button>
		      <!--<el-button @click.native.prevent="handleReset2">重置</el-button>-->
		    </el-form-item>
	  	</el-form>
	</div>
</template>

<script>
import qs from 'qs';
import MenuUtils from '@/utils/MenuUtils'
import md5 from 'js-md5';
import axios from 'axios'
import NProgress from 'nprogress'
import SIdentify from '@/components/loginCheck'

	var routers = [],code='';
  export default {
    data() {
      return {
        logining: false,
        ruleForm2: {
          account: '',
          checkPass: ''
        },
        picLyanzhengma:'',
		checkCode:'',
		identifyCodes:'',
		identifyCode:'',
		localtype:'2',
        rules2: {
          account: [
            { required: true, message: '请输入账号', trigger: 'blur' },
          ],
          checkPass: [
            { required: true, message: '请输入密码', trigger: 'blur' },
          ]
        },
		rootPath:'https://um.qyfzilot.cn',
        checked: false
      };
    },
    components:{
    	SIdentify
    },
    mounted(){
		localStorage.clear();
		this.getUrlpath();
		this.getQueryStringByName()
		var userAgent = navigator.userAgent; //取得浏览器的userAgent字符串
	    //判断是否chorme浏览器
	    if (userAgent.indexOf("Chrome") > -1){
	    }else{
			this.$message({
				message:'为了您能更好的使用本系统,建议使用谷歌浏览器;https://pc.qq.com/detail/1/detail_2661.html',
				type:'error',
				duration:0
			});
	    }
    		this.identifyCodes = '1234567890abcdefjhjkmnpqrstuvwxy';
    		this.identifyCode = "";
    		this.makeCode(this.identifyCodes, 4);
    		this.$nextTick(function(){
		     this.$refs.account.focus()
		})
    },
    methods: {
		getUrlpath(){
			let sourceStr = location.href.split('/')[2];
			if(sourceStr =='wxpromotion.qyfzilot.cn'){
				this.rootPath = 'https://um.qyfzilot.cn'
			}else if(sourceStr =='test-wxpromotion.qyfzilot.cn'){
				this.rootPath = 'https://test-um.qyfzilot.cn'
			}else{
				if(this.localtype==1){
					this.rootPath = 'https://um.qyfzilot.cn'
				}else{
					this.rootPath = 'https://test-um.qyfzilot.cn'
				}
			}
		},
        getQueryStringByName() {
            var url = window.location.href; //获取url中"?"符后的字串
            var theRequest = new Object();
            if (url.indexOf("?") != -1) {
				var str = url.split("?")[1];
                var strs = str.split("&");
                for(var i = 0; i < strs.length; i ++) {

                    theRequest[strs[i].split("=")[0]]=decodeURI(strs[i].split("=")[1]);

                }
            }
            if(theRequest.token){
                this.getUserModule(theRequest.token,theRequest.id,theRequest.cookies)
			}else{
                return ;
			}
        },
        login(data){
	       	localStorage.setItem('routers', JSON.stringify(data));
	        MenuUtils(routers,data)
        },
       randomNum(min, max) {
	      return Math.floor(Math.random() * (max - min) + min);
	    },
	 //-------------------图片验证码start-------------------------//
	    refreshCode() {
	      this.identifyCode = "";
	      this.makeCode(this.identifyCodes, 4);
	    },
	    makeCode(o, l) {
	      for (let i = 0; i < l; i++) {
	        this.identifyCode += this.identifyCodes[
	          this.randomNum(0, this.identifyCodes.length)
	        ];
	      }
	    },
       checkLpicma(){
       		//取得输入的验证码并转化为大写
          if(this.picLyanzhengma == '') {
            		this.$message.error('请输入验证码');
          }else if(this.picLyanzhengma.toLowerCase() != this.identifyCode ) { //若输入的验证码与产生的验证码不一致时
              this.refreshCode();//刷新验证码
              this.picLyanzhengma = '';
          }else { //输入正确时
            return true;
          }
	   },
	    //获取角色模块//
		getUserModule(token,id,cookies){
            axios.defaults.headers.common['token'] = token;
            axios.defaults.headers.common['user_cookie'] = cookies;
            let self = this;
            let sendData = { userId:id };
            localStorage.setItem('token', token);
            localStorage.setItem('userid',id);
            localStorage.setItem('cookies',cookies);
            this.$http({
                method:'get',
                url:'/v2/biz/back/user/user-moudel',
                params:sendData,
                headers:{'Accept': 'application/json'},
            })
			.then(res =>{
				if(res.data.code!==200){
					self.$message({
						message: res.data.message,
						type: 'error'
					});
				}else{
					let str = JSON.stringify(res.data.data);
					str = str.replace(/url/g,"path");
					str = str.replace(/label/g,"name");
					let arr = JSON.parse(str);
					for(var i=0;i<arr.length;i++){
						arr[i].label = arr[i].path.slice(1);
						arr[i].path = '/home';
						arr[i].component = 'Home';
						arr[i].leaf =true;
						arr[i].redirect = arr[i].children[0].path;
						if(arr[i].children){
							arr[i].leaf = false;
							for(var a=0;a<arr[i].children.length;a++){
								arr[i].children[a].component = arr[i].children[a].path.slice(1);
								arr[i].children[a].parent = arr[i].label;
								arr[i].children[a].leaf =true;
							}
						}else{
							arr[i].leaf = true;
						}
					}
					self.login(arr);
					self.$router.addRoutes(routers);
					self.$router.push({ path: '/home' });
					location.reload();
				}
			})
		},
    	//-------------------图片验证码end-------------------------//
      handleSubmit2(ev) {
      	// if(this.checkLpicma()){
      	this.$refs.ruleForm2.validate((valid) => {
			if (valid) {
				this.logining = true;
				NProgress.start();
				let loginParams = { userName: this.ruleForm2.account, password: md5(this.ruleForm2.checkPass),type:'2'};
				axios.defaults.headers.common['token'] = 'a095bc6f516a87ba65f91c85e5621265';
				let self = this;
				this.$http({
					method:'post',
					url:self.rootPath+'/v1/back/user/login',
					async:false,
					data:qs.stringify(loginParams),
					headers:{'Accept': 'application/json'},
				})
				.then(data => {
					  self.logining = false;
							NProgress.done();
					  if (data.data.code !== 200) {
							self.$message({
							  message: data.data.message,
							  type: 'error'
							});
					  } else {
						 let info = data.data.data;
						 let usertoken = info.token;
						 localStorage.setItem('token', usertoken);
						 console.log(usertoken);
						 axios.defaults.headers.common['token'] = usertoken;
						 let userCookie = info.cookie;
						 localStorage.setItem('cookies', userCookie);
						 axios.defaults.headers.common['user_cookie'] = userCookie;
						 let sendData = { userId:info.id };
						 console.log(sendData)
						 localStorage.setItem('user',info.userName);
						 localStorage.setItem('userid',info.id);
						self.$router.push({ path: '/home' });
						}
					})
					.catch (data=>{
						self.logining = false;
					})	
               	}
            })
        }      
    }
  }

</script>

<style lang="scss" scoped>
	.box{
		z-index: 2;
	}
   .box_img{
		width: 660px;
		margin: 180px auto;
    		position: relative;
	}
  .login-container {
    /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
   	margin: -580px auto;
    position: relative;
    width: 360px;
    padding: 5px 35px 15px 15px;
    background: #fff;
    .title {
      margin: 0px auto 40px auto;
      text-align: center;
      color: #505458;
       .logo{
	    		width: 30px;
	    		text-align: center;
	    }
    }
    .remember {
      margin: 0px 0px 35px 0px;
    }
  }
  .code {
  		margin-left:245px;
  		position: relative;
  		top: -40px;
	  	width: 114px;
	  	height: 40px;
	  	border: 1px solid #3b78ff;
	}
	.submit_Btn{
		border-radius: 0 !important;
		background: #FFF !important;
		color:#3b78ff ;
		border: 1px solid #3b78ff !important;
		font-weight: normal;
		font-size: 16px;
	}
</style>
<style lang="scss">
	.login{
		position: absolute;
	    top: 0;
	    left: 0;
	    right: 0;
	    bottom: 0;
	    background: url(../../static/images/bg.png)  !important;
		background-repeat:no-repeat !important;
		background-position-y:85% !important;
		background-size:100% 100%;
		.el-input__inner{
			border: 1px solid #3b78ff !important;
			border-radius: 0 !important;
			text-indent: 20px;
		}
		.el-input__inner::-webkit-input-placeholder{
			color:#90b1fa ;
		}

	}

</style>



