import Vue from 'vue'
import CryptoJS from 'crypto-js'
export default {//加密
    encrypt(word){
        var key = CryptoJS.enc.Utf8.parse('szzhongyuanzilot');
        var iv  = CryptoJS.enc.Utf8.parse('zilotszzhongyuan');
        var encrypted = CryptoJS.AES.encrypt(word, key, { iv: iv,mode:CryptoJS.mode.CBC, padding: CryptoJS.pad.Pkcs7});  //CryptoJS.pad.Pkcs7

        return encrypted.toString();
    },
    //解密
    decrypt(word){
        var key = CryptoJS.enc.Utf8.parse('szzhongyuanzilot');
        var iv  = CryptoJS.enc.Utf8.parse('zilotszzhongyuan');
        var decrypted = CryptoJS.AES.decrypt(word, key, { iv: iv,mode:CryptoJS.mode.CBC, padding: CryptoJS.pad.Pkcs7});  //CryptoJS.pad.Pkc
        return CryptoJS.enc.Utf8.stringify(decrypted).toString();
    }

}



